.ButtonWrapper {
  border: 1px solid #1360B1;
  margin: 0 35px;
  background-color: transparent; }
  .ButtonWrapper:hover {
    background: white;
    -webkit-box-shadow: 0px 0px 50px 10px #0854AA;
            box-shadow: 0px 0px 50px 10px #0854AA; }
    .ButtonWrapper:hover .MainButton {
